/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #f1dae8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.no-webp .inicio {
  background-image: url(../img/donacion.jpg);
}

.webp .inicio {
  background-image: url(../img/donacion.webp);
}

.header {
  background-color: #850069;
  padding: 0.5rem 5%;
}
.header .logo1 {
  padding: 1px;
}
.header .logo {
  width: 100%;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: 0.5s;
}
.header .logo:hover {
  border: 2px solid #cc459a;
  box-shadow: 0 0 10px #cc459a, 0 0 40px #cc459a, 0 0 70px #cc459a;
}

.inicio .overlay {
  position: absolute;
  background: rgb(85, 0, 255);
  background: linear-gradient(270deg, rgba(85, 0, 255, 0.3) 0%, rgba(255, 0, 229, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenedor.inicio {
  background-position: center center;
  background-size: cover;
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  width: 100%;
  max-width: 100%;
  margin: 0 0 0 0;
  overflow: hidden;
}
.contenedor.inicio h1 {
  color: #FFFFFF;
  margin: 0 0 2.5rem 0;
}

.contenido-imagen {
  border-radius: 5px solid #FFFFFF;
  padding: 20px;
  width: 63rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.footer {
  background-color: #2d63a8;
}
.footer .navegacion {
  display: flex;
  justify-content: center;
  margin-top: 0.5rem;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 0rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

#check {
  display: none;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
@media (max-width: 769px) {
  .navegacion {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 0;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(50px);
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    overflow: hidden;
    transition: 0.3s ease;
  }
  .navegacion a {
    transform: translateY(-200px);
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
  }
  .navegacion a:last-of-type {
    margin-top: 0;
  }
}
.navegacion a:hover {
  color: #FFFFFF;
  text-shadow: 0 0 10px #FFFFFF, 0 0 40px #FFFFFF, 0 0 70px #FFFFFF;
}

.icons {
  display: inline-flex;
  font-size: 5rem;
  color: #FFFFFF;
  cursor: pointer;
  position: absolute;
  right: 5%;
}
@media (min-width: 768px) {
  .icons {
    display: none;
  }
}

#check:checked ~ .navegacion {
  height: 17.7rem;
}

#check:checked ~ .navegacion a {
  transform: translateY(10px);
  transition-delay: calc(0.1s * var(--i));
}

#close-icon {
  display: none;
}

#check:checked ~ .icons #menu-icon {
  display: none;
}

#check:checked ~ .icons #close-icon {
  display: block;
}

.no-webp .inicio {
  background-image: url(../img/donacion.jpg) no-repeat;
}

.webp .inicio {
  background-image: url(../img/donacion.webp) no-repeat;
}

.header-base {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
}
.header-base h1 {
  color: #FFFFFF;
  margin: 0 0 2.5rem 0;
}
.header-base .logo1 {
  padding: 1px;
}
.header-base .logo {
  width: 100%;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: 0.5s;
}
.header-base .logo:hover {
  border: 2px solid #cc459a;
  box-shadow: 0 0 10px #cc459a, 0 0 40px #cc459a, 0 0 70px #cc459a;
}

.barra-base {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.5rem 10%;
  display: flex;
  justify-content: space-between;
  z-index: 100;
}
.barra-base::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(133, 2, 255, 0.7);
  backdrop-filter: blur(10px);
  z-index: -1;
}

.inicio .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(85, 0, 255, 0.3) 0%, rgba(255, 0, 229, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenedor.inicio {
  background-position: center center;
  background-size: cover;
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  width: 100%;
  max-width: 100%;
  margin: 0 0 0 0;
  overflow: hidden;
}

.contenido-imagen {
  border-radius: 5px solid #FFFFFF;
  padding: 20px;
  width: 63rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}
@media (min-width: 768px) {
  .contenedor-card {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    position: relative;
  }
}
@media (max-width: 768px) {
  .contenedor-card {
    display: grid;
    gap: 5rem 2rem;
  }
}
.contenedor-card {
  margin-bottom: 5rem;
}

.card {
  background-color: #ce4d9e;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  height: 300px;
  border-radius: 20px;
  box-shadow: 0px 16px 17px -6px rgba(0, 0, 0, 0.33);
  transition: 0.5s;
}
.card .imagen-card {
  position: absolute;
  top: 0;
  width: 100%;
  height: 220px;
  border-radius: 12px;
  transition: 0.5s;
  overflow: hidden;
}
.card .imagen-card img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card .contenido-card {
  position: absolute;
  top: 200px;
  width: 100%;
  padding: 0 3rem;
  height: 3rem;
  text-align: center;
  transition: 0.5s;
}
.card .contenido-card h2 {
  color: #FFFFFF;
  overflow: auto;
}
.card .contenido-card p {
  overflow: hidden;
  height: 1rem;
}
.card:hover {
  height: 500px;
}
.card:hover .card {
  gap: 0 0 8rem 0;
}
.card:hover .imagen-card {
  top: -45px;
  scale: 0.75;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.5);
}
.card:hover .contenido-card {
  top: 150px;
  height: 250px;
}
.card:hover .contenido-card p {
  top: 100px;
  height: -webkit-fill-available;
}

@media (min-width: 768px) {
  .seccion-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
@media (max-width: 768px) {
  .imagen img {
    border-radius: 12px;
    height: 100%;
    width: 100%;
  }
}
.entrada-blog {
  position: relative;
  height: auto;
  border-radius: 20px;
  margin-bottom: 2rem;
}
.entrada-blog p {
  height: -webkit-fill-available;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
  .entrada-blog .imagen img {
    border-radius: 12px;
    height: 100%;
    left: 0;
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    width: 48%;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada {
  display: flex;
  flex-direction: column;
}
.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.5rem;
  background-color: #a03b8a;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #F9511F;
}